export default {
  namespaced: true,

  state: {
    activeMissions: [],
    selectedArea: null,
    maxMissions: 3,

    // 
    areas: {
      garden: {
        name: "",
        description: "",
        distance: 1,
        baseDuration: 30, // 
        reward: {
          common: { min: 1, max: 3 },
          wildflower: { min: 0, max: 1 },
          mountain: { min: 0, max: 0 },
          rare: { min: 0, max: 0 },
        },
        risk: 0.05, // 5% 
        beeCount: 1,
      },
      meadow: {
        name: "",
        description: "",
        distance: 2,
        baseDuration: 60, // 
        reward: {
          common: { min: 2, max: 4 },
          wildflower: { min: 1, max: 2 },
          mountain: { min: 0, max: 0.5 },
          rare: { min: 0, max: 0.1 },
        },
        risk: 0.1, // 10% 
        beeCount: 2,
      },
      forest: {
        name: "",
        description: "",
        distance: 3,
        baseDuration: 120, // 
        reward: {
          common: { min: 1, max: 2 },
          wildflower: { min: 2, max: 4 },
          mountain: { min: 1, max: 2 },
          rare: { min: 0.1, max: 0.5 },
        },
        risk: 0.15, // 15% 
        beeCount: 3,
      },
      mountain: {
        name: "",
        description: "",
        distance: 5,
        baseDuration: 180, // 
        reward: {
          common: { min: 0, max: 1 },
          wildflower: { min: 1, max: 2 },
          mountain: { min: 2, max: 4 },
          rare: { min: 0.5, max: 1 },
        },
        risk: 0.25, // 25% 
        beeCount: 4,
      },
    },

    // 
    honeyCollected: {
      common: 0,
      wildflower: 0,
      mountain: 0,
      rare: 0,
    },

    missionStats: {
      totalMissions: 0,
      successMissions: 0,
      failedMissions: 0,
    },
    missionHistory: [], // 
  },

  getters: {
    canStartMission(state, getters, rootState) {
      const availableBees = rootState.bees.bees;
      if (state.selectedArea === null) return false;

      const areaData = state.areas[state.selectedArea];

      // 
      const beesNeeded = areaData.beeCount;

      // 
      const assignedBees = state.activeMissions.reduce((total, mission) => {
        return total + state.areas[mission.area].beeCount;
      }, 0);

      return (
        availableBees - assignedBees >= beesNeeded &&
        state.activeMissions.length < state.maxMissions
      );
    },

    foragingStats(state) {
      return {
        totalMissions: state.activeMissions.length,
        maxMissions: state.maxMissions,
        totalHoneyCollected: Object.values(state.honeyCollected).reduce(
          (a, b) => a + b,
          0
        ),
      };
    },
  },

  mutations: {
    SET_SELECTED_AREA(state, area) {
      state.selectedArea = area;
    },

    START_MISSION(state, mission) {
      state.activeMissions.push(mission);
    },

    COMPLETE_MISSION(state, missionId) {
      const index = state.activeMissions.findIndex((m) => m.id === missionId);
      if (index !== -1) {
        state.activeMissions.splice(index, 1);
      }
    },

    ADD_COLLECTED_HONEY(state, { type, amount }) {
      state.honeyCollected[type] += amount;
    },

    SET_FORAGING_DATA(state, data) {
      if (data.activeMissions) state.activeMissions = data.activeMissions;
      if (data.selectedArea) state.selectedArea = data.selectedArea;
      if (data.honeyCollected) state.honeyCollected = data.honeyCollected;
      if (data.maxMissions) state.maxMissions = data.maxMissions;
      if (data.missionHistory) {
        console.log(":", data.missionHistory);
        state.missionHistory = data.missionHistory;
      }
      if (data.missionStats) {
        console.log(":", data.missionStats);
        state.missionStats = data.missionStats;
      }
    },

    ADD_MISSION_RECORD(state, record) {
      // 
      state.missionStats.totalMissions++;
      if (record.success) {
        state.missionStats.successMissions++;
      } else {
        state.missionStats.failedMissions++;
      }

      // 10
      state.missionHistory.unshift(record);
      if (state.missionHistory.length > 10) {
        state.missionHistory.pop();
      }
    },
  },

  actions: {
    // 
    selectArea({ commit }, area) {
      commit("SET_SELECTED_AREA", area);
    },

    // 
    startForagingMission(
      { state, commit, rootState, rootGetters, dispatch },
      missionData
    ) {
      if (!rootGetters["foraging/canStartMission"]) {
        dispatch(
          "showNotification",
          {
            message: "",
            type: "warning",
          },
          { root: true }
        );
        return;
      }

      const area = missionData.area;
      const areaData = state.areas[area];

      // 
      let baseDuration = areaData.baseDuration;
      
      if (missionData.duration === 'medium') {
        baseDuration = baseDuration * 2; // 
      } else if (missionData.duration === 'long') {
        baseDuration = baseDuration * 4; // 
      }
      
      console.log(`: =${areaData.baseDuration}, =${missionData.duration}, =${baseDuration}`);
      
      // 
      const weatherMultiplier = rootState.weather.effectMultiplier || 1.0;
      const duration = Math.ceil(baseDuration / weatherMultiplier);

      // 
      const mission = {
        id: Date.now(), // ID
        area: area,
        bees: missionData.bees,
        risk: missionData.risk,
        duration: missionData.duration, // 
        durationSeconds: duration, // 
        startTime: Date.now(),
        endTime: Date.now() + duration * 1000,
        inProgress: true,
      };

      commit("START_MISSION", mission);

      let timeDisplay;
      if (duration < 60) {
        timeDisplay = `${duration} `;
      } else {
        const minutes = Math.floor(duration / 60);
        const seconds = duration % 60;
        timeDisplay =
          seconds > 0 ? `${minutes}  ${seconds} ` : `${minutes} `;
      }

      dispatch(
        "showNotification",
        {
          message: ` ${missionData.bees}  ${areaData.name}  ${timeDisplay}`,
          type: "info",
        },
        { root: true }
      );

      // 
      setTimeout(() => {
        dispatch("completeMission", mission.id);
      }, duration * 1000);
    },

    // 
    completeMission({ state, commit, dispatch }, missionId) {
      const missionIndex = state.activeMissions.findIndex(
        (m) => m.id === missionId
      );
      if (missionIndex === -1) return;
    
      const mission = state.activeMissions[missionIndex];
      const areaData = state.areas[mission.area];
    
      //  ()
      const roll = Math.random();
      const success = roll > areaData.risk;
    
      //  - 
      const missionRecord = {
        id: mission.id,
        area: mission.area,
        date: Date.now(),
        bees: mission.bees, // 
        risk: mission.risk, // 
        success: success,
        rewards: {},
        lostBees: 0,
      };

      if (success) {
        // 
        const rewards = {};
        // let totalHoney = 0;

        for (const [type, range] of Object.entries(areaData.reward)) {
          if (range.max <= 0) continue;

          const amount = Math.random() * (range.max - range.min) + range.min;
          if (amount > 0) {
            rewards[type] = Number(amount.toFixed(1));
            // totalHoney += rewards[type];

            // 
            commit("ADD_COLLECTED_HONEY", { type, amount: rewards[type] });

            // 
            commit(
              "honey/ADD_HONEY_BY_TYPE",
              { type, amount: rewards[type] },
              { root: true }
            );
          }
        }

        // 
        missionRecord.rewards = rewards;

        // 
        let rewardText = Object.entries(rewards)
          .map(
            ([type, amount]) =>
              `${amount}kg ${
                state.honeyTypes ? state.honeyTypes[type].name : type
              }`
          )
          .join("");

        dispatch(
          "showNotification",
          {
            message: ` ${areaData.name}  ${rewardText}`,
            type: "success",
          },
          { root: true }
        );
      } else {
        // 
        // 0-1
        const lostBees = Math.random() < 0.3 ? 1 : 0;
        missionRecord.lostBees = lostBees;

        // 
        if (lostBees > 0) {
          commit("bees/REMOVE_BEES", lostBees, { root: true });
        }

        dispatch(
          "showNotification",
          {
            message: ` ${areaData.name} ${
              lostBees > 0 ? ` ${lostBees} ` : ""
            }`,
            type: "danger",
          },
          { root: true }
        );
      }

      // 
      commit("ADD_MISSION_RECORD", missionRecord);

      // 
      commit("COMPLETE_MISSION", missionId);
    },

    // 
    upgradeForaging({ state, commit, rootState, dispatch }, upgradeType) {
      const cost = 200; // 

      if (rootState.money < cost) {
        dispatch(
          "showNotification",
          {
            message: ` ${cost} `,
            type: "warning",
          },
          { root: true }
        );
        return;
      }

      commit("REMOVE_MONEY", cost, { root: true });

      if (upgradeType === "capacity") {
        // 
        commit("SET_FORAGING_DATA", {
          ...state,
          maxMissions: state.maxMissions + 1,
        });

        dispatch(
          "showNotification",
          {
            message: ` ${state.maxMissions} `,
            type: "success",
          },
          { root: true }
        );
      } else if (upgradeType === "efficiency") {
        //  ()
        const updatedAreas = { ...state.areas };

        for (const area in updatedAreas) {
          for (const honeyType in updatedAreas[area].reward) {
            updatedAreas[area].reward[honeyType].min *= 1.2;
            updatedAreas[area].reward[honeyType].max *= 1.2;
          }

          // 
          updatedAreas[area].risk = Math.max(
            0.01,
            updatedAreas[area].risk * 0.8
          );

          // 
          updatedAreas[area].baseDuration = Math.max(
            15,
            Math.floor(updatedAreas[area].baseDuration * 0.9)
          );
        }

        commit("SET_FORAGING_DATA", {
          ...state,
          areas: updatedAreas,
        });

        dispatch(
          "showNotification",
          {
            message: "",
            type: "success",
          },
          { root: true }
        );
      }
    },
  },
};
